.container {
  max-width: 480px;
  margin: 120px auto 282px;
}

.flexColumnCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.displayFlex {
  display: flex;
  justify-content: center;
}

.text {
  margin: 0 8px 0 0;
  color: #8585AD;
}

.link {
  margin: 0;
  color: #4C4CFF;
}

.link:hover {
  color: white;
}